*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

body{
    font-family: 'Poppins', 'sans-serif';
}

a.navbar-brand .navbar-light,
.navbar-light a.navbar-brand, .form{
font-weight: 900;
line-height: 72px;
letter-spacing: 1px;
text-align: left;
font-size:48px;
color: #121C33;
}

.main{
    background-color: #F2F2F2;
}

.sidebar{
    position:fixed;
    right:0;
    top: 0;
    transform: translate(100);
    height:100%;
    overflow: scroll;
    background-color: white;
    border-radius: 30px;
}

 .close{
  cursor: pointer;
}

.box{
border-radius: 30px;
background-color: white;
width: 100px;
height: auto;
cursor: pointer;
}

.name{
 font-family: 'sans serif';
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 24px;
color: #121C33;
}

.name1{
    font-size: 15px;
   line-height: 18px;
}

.pics{
   width:72px;
   height:72px;
   object-fit: cover;
   border-radius: 8px;
}

.icon {
    padding: 80px;
    position: fixed;
    border-radius: 50%;
    bottom: 1.2rem;  
    right:3.5rem;     
    cursor: pointer;
}

.planet{
 font-family: 'Poppins';
font-style: normal;
font-weight: 900;
font-size: 48px;
line-height: 72px;
display: flex;
align-items: center;
letter-spacing: 1px;
color: #121C33;
}

